import { connect } from "react-redux";
import React from "react";
import ActionEnum from "../actions/actionEnum.js";

const TimeRow = (props) => {
  const handleTimeIn = (event) => {
    let time = event.target.value;
    //I want to update the global array with the value specific to this table entry, is this a dumb way of doing this? Or even the right way to handle this?
    //As of now, I'm just attaching the event values and index of this row to the Action object, and the actual functionality is handled in the reducer.
    props.updateTimeIn(props.index, time);
  };

  const handleTimeOut = (event) => {
    let time = event.target.value;
    props.updateTimeOut(props.index, time);
  };

  return (
    <tr>
      <td>
        <form>
          <input
            type="time"
            onChange={(event) => {
              handleTimeIn(event);
            }}
          />
        </form>
      </td>
      <td>
        <form>
          <input
            type="time"
            onChange={(event) => {
              handleTimeOut(event);
            }}
          />
        </form>
      </td>
      <td>{props.timeChunks[props.index].timeDiff.toFixed(2)}</td>
    </tr>
  );
};

const mapStateToProps = (state) => {
  return {
    totalTime: state.totalTime,
    timeChunks: state.timeChunks,
  };
};

const mapDispatchToProps = (dispatch) => ({
  updateTimeIn: (index, time) =>
    dispatch({
      type: ActionEnum.CHANGE_TIME_IN,
      index: index,
      time: time,
    }),
  updateTimeOut: (index, time) =>
    dispatch({
      type: ActionEnum.CHANGE_TIME_OUT,
      index: index,
      time: time,
    }),
});

export default connect(mapStateToProps, mapDispatchToProps)(TimeRow);
