import { connect } from "react-redux";
import TimeRow from "./timeRow.jsx";
import TimePredictor from "./timePredictor.jsx";
import React from "react";
import ActionEnum from "../actions/actionEnum.js";


//Time logger is like the main thing of our page. Holds all the different components.
const TimeLogger = (props) => {
  //pretty simple, these guys just increase or decrease table size.
  const handleAddEntry = (event) => {
    props.increaseSize();
  };

  const handleRemoveEntry = (event) => {
    props.decreaseSize();
  };

  //probably a more efficient way to do this
  let sum = 0;
  for(let i = 0; i < props.timeChunks.length; i++) {sum += props.timeChunks[i].timeDiff; }  


  return (
    <div className="container">
      <table>
        <thead>
          <tr>
            <th>Time In</th>
            <th>Time Out</th>
            <th>Time Delta (hrs)</th>
          </tr>
        </thead>
        <tbody>
          {Array.from(Array(props.tableSize)).map((x, index) => (
            <TimeRow key={index} index={index} />
          ))}
        </tbody>
      </table>
      <button
        onClick={(event) => {
          handleAddEntry(event);
        }}
      >
        ADD ENTRY
      </button>
      <button
        id="remove"
        onClick={(event) => {
          handleRemoveEntry(event);
        }}
      >
        REMOVE ENTRY
      </button>
      <h1>Total Hours Worked:</h1>

      <TimePredictor hourSum={sum}/>
    </div>
  );
};

//map state to props transforms the redux state into a prop object with the desired fields.
const mapStateToProps = (state) => {
  return {
    totalTime: state.totalTime,
    timeChunks: state.timeChunks,
    tableSize: state.tableSize,
  };
};

const mapDispatchToProps = dispatch => ({
  increaseSize: () => dispatch({ type: ActionEnum.INC_SIZE }),
  decreaseSize: () => dispatch({ type: ActionEnum.DEC_SIZE }),
});

export default connect(mapStateToProps, mapDispatchToProps)(TimeLogger);
