import ActionEnum from "../actions/actionEnum.js";
import { calculateTimeDifference } from "../util/clockUtil.js";

const initialState = {
  timeChunks: [{ timeIn: "", timeOut: "", timeDiff: 0 }], //a list of objects with three fields: one that corresponds to time in, another that corresponds to time out, and another that is difference between these two times.
  totalTime: 0,
  tableSize: 1, //number of table row components to render.
};

//check to see if both the time in and out are filled out.
const checkIfComplete = (obj) => {
  return obj.timeIn !== "" && obj.timeOut !== "";
};

//reducer always takes in the current state, and returns a NEW STATE
const reducer = (state = initialState, action) => {
  //deep copy reference
  let clone = state.timeChunks.slice();
  let temp;

  switch (action.type) {
    case ActionEnum.INC_SIZE:
      let entry = { timeIn: "", timeOut: "", timeDiff: 0 };
      clone.push(entry);
      return { ...state, timeChunks: clone, tableSize: state.tableSize + 1 };
    case ActionEnum.DEC_SIZE:
      if (state.tableSize - 1 !== 0) {
        clone.pop();
        return { ...state, timeChunks: clone, tableSize: state.tableSize - 1 };
      } else return state;

    case ActionEnum.CHANGE_TIME_IN:
      temp = clone[action.index]; // save object here as reference.
      temp.timeIn = action.time;
      if (checkIfComplete(temp)) {
        temp.timeDiff = calculateTimeDifference(temp.timeIn, temp.timeOut);
      }
      return { ...state, timeChunks: clone };
    case ActionEnum.CHANGE_TIME_OUT:
      temp = clone[action.index];
      temp.timeOut = action.time;
      if (checkIfComplete(temp)) {
        temp.timeDiff = calculateTimeDifference(temp.timeIn, temp.timeOut);
      }
      return { ...state, timeChunks: clone };
    default:
      return state;
  }
};

export default reducer;
