import { connect } from "react-redux";
import React, { useState, useEffect } from "react";
import { calcExpectedEnd } from "../util/clockUtil.js";

const TimePredictor = (props) => {
  //just a bunch of local states, we could probably shove them into the reducer but  I feel like that is unneccessary
  const [expectedEnd, setExpectedEnd] = useState(""); //count is initialized to zero.
  const [shiftStart, setShiftStart] = useState("");
  const [shiftType, setShiftType] = useState("start working");
  const [desiredHours, setDesiredHours] = useState(0);
  const [hoursWorked, setHoursWorked] = useState(props.hourSum)
  
  if(props.hourSum !== hoursWorked) {
    setHoursWorked(props.hourSum);
  }

  //wrapper function for the util function.
  const calculateExpectedEnd = () => {
    let temp = calcExpectedEnd(
      hoursWorked,
      desiredHours,
      shiftStart,
      shiftType
    );
    setExpectedEnd(temp);
  };

  //calculateExpectedEnd()  was one step behind the current state, this forces it to get the last state update (I think).
  //curious if this is the right way of fixing this problem.
  useEffect(() => {
    calculateExpectedEnd(); 
  }, [desiredHours, shiftType, shiftStart, hoursWorked]);

  return (
    <div>
      <h2>
        <b>{hoursWorked.toFixed(2)}</b>
      </h2>
      <h3>
        If I
        <select
          onChange={(event) => {
            let t = event.target.value;
            setShiftType(t);
          }}
        >
          <option>start working</option>
          <option>return from my break</option>
        </select>
        at
        <input
          id="desiredTime"
          type="time"
          onChange={(event) => {
            setShiftStart(event.target.value);
            console.log(shiftStart);
          }}
        />
        and I want to work at least
        <input
          type="number"
          min="0"
          onChange={(event) => {
            setDesiredHours(event.target.value);
          }}
        ></input>
        hours today, then I should keep working until...
      </h3>
      <h2>
        <b>{expectedEnd}</b>
      </h2>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    totalTime: state.totalTime,
    timeChunks: state.timeChunks,
  };
};



export default connect(mapStateToProps)(TimePredictor);
