//this file is just a bunch of functions to use for calculating clock stuff.

export const calculateTimeDifference = (timeIn, timeOut) => {
  let clockIn = timeIn.split(":");
  let clockOut = timeOut.split(":");

  let hourDiff = parseInt(clockOut[0]) - parseInt(clockIn[0]);
  let minDiff = parseInt(clockOut[1]) - parseInt(clockIn[1]);

  minDiff /= 60;

  return Math.abs(hourDiff + minDiff);
  //this.timeDiff = Math.abs(hourDiff + minDiff);
  //this.tableRef.updateDiffList(this.timeDiff, this.index);
};

//helper
const convertDesiredHours = (num) => {
  let timeString;
  if (num % 1 === 0) {
    timeString = num + ":00";
  } else {
    let minutes = Math.round((num % 1) * 60);
    let hours = Math.trunc(num);
    timeString = hours + ":" + minutes;
  }
  return timeString;
};
//helper
const padZeros = (num) => {
  let s = num + "";
  while (s.length < 2) s = "0" + s;
  return s;
};

export const calcExpectedEnd = (
  curHours,
  desiredHours,
  shiftStart,
  shiftType
) => {
  let dHours;
  if (shiftStart === "") return;
  //is much better to use an enum, I got lazy :\
  if (shiftType === "return from my break") dHours = desiredHours - curHours;
  else if (shiftType === "start working") dHours = desiredHours;

  //if (isNaN(dHours)) return "Enter desired hours!";
  if (dHours <= 0) {
    return "You've already worked this amount of time!";
  }

  let startTime = shiftStart.split(":");
  let hoursToAdd = convertDesiredHours(dHours).split(":"); //convert desired hours to expected format, then split on ':' into to substrings.

  let endMins = parseInt(startTime[1]) + parseInt(hoursToAdd[1]);
  let endHours = parseInt(startTime[0]) + parseInt(hoursToAdd[0]);
  let ampm;

  //go up in hours if minutes to add is greater than 60.
  if (endMins >= 60) {
    endMins %= 60;
    endHours++;
  }

  endHours >= 12 && endHours < 24 ? (ampm = "PM") : (ampm = "AM");
  if (endHours !== 12) endHours %= 12;
  if (endHours === 0) endHours = 12;

  //pad with zeroes after this.
  endHours = padZeros(endHours);
  endMins = padZeros(endMins);
  return endHours + ":" + endMins + " " + ampm;
};
